import type { AnyClient } from "@gadgetinc/api-client-core";
import { createContext, useContext } from "react";

export type GadgetShopifyUiExtensionContextValue = {
  /** Whether or not the api client is ready to make requests to with Shopify session token authentication */
  ready: boolean;
  /** An instance of the Gadget application's api client set up to use Shopify session token authentication */
  api: AnyClient;
};

export const GadgetShopifyUiExtensionContext = createContext<GadgetShopifyUiExtensionContextValue>({
  ready: false,
  api: null as unknown as AnyClient,
});

export const useGadget = () => useContext<GadgetShopifyUiExtensionContextValue>(GadgetShopifyUiExtensionContext);
